import React, { Component } from 'react'
import Auth from '../auth/Authenticate'
import { withApollo } from 'react-apollo'
import gql from 'graphql-tag'
import {
	Row,
	Col,
	Form,
	Typography,
	Icon,
	Input,
	Button,
	Checkbox,
	Divider
} from 'antd'
import './Login.scss'
import { Link } from 'react-router-dom'

const { Title } = Typography

export class Login extends Component {
	constructor(props) {
		super(props)
		this.state = {
			email: 'chin@gmail.com',
			password: 'd3f4ultP4ssword!',
			errors: []
		}
	}
	componentWillMount() {
		const token = localStorage.getItem('token')
		if (token) {
			this.props.history.push('/')
		}
	}
	onChange = e => {
		this.setState({
			[e.target.name]: e.target.value
		})
	}
	onLogin = e => {
		e.preventDefault()

		const { client } = this.props
		const { email, password } = this.state

		client
			.mutate({
				mutation: USER_LOGIN,
				variables: {
					userInput: {
						email,
						password
					}
				}
			})
			.then(res => {
				Auth.authenticate(() => {
					localStorage.setItem('access-token', res.data.login.token)
					this.props.history.push('/')
				})
			})
			.catch(res => {
				console.log(res)
				const errors = res.graphQLErrors.map(error => error.message)
				this.setState({
					errors
				})
			})
	}
	handleSubmit = e => {
		e.preventDefault()
		this.props.form.validateFields((err, values) => {
			if (!err) {
				console.log('Received values of form: ', values)
			}
		})
	}
	render() {
		const { getFieldDecorator } = this.props.form
		const { email, password, errors } = this.state
		return (
			<>
				{/* <div
					style={{
						margin: 'auto',
						width: '50%',
						border: '3px solid green',
						padding: '10px'
					}}
				>
					Login
					<form onSubmit={this.onLogin}>
						<input name="email" onChange={this.onChange} value={email} />
						<br />
						<input
							type="password"
							name="password"
							onChange={this.onChange}
							value={password}
						/>
						<br />
						<button type="submit">Login</button>
					</form>
					{errors && errors.map((error, i) => <h4 key={i}>{error}</h4>)}
				</div> */}
				<Row
					style={{
						backgroundColor: 'pink'
					}}
				>
					<Col
						xs={{ span: 24, offset: 0 }}
						sm={{ span: 16, offset: 8 }}
						lg={{ span: 7, offset: 17 }}
					>
						<div id="components-form-demo-normal-login">
							<Form onSubmit={this.handleSubmit} className="login-form">
								<div className="login-form-header">
									<Title level={2}>Chnirt</Title>
								</div>
								<Form.Item>
									{getFieldDecorator('userName', {
										rules: [
											{
												required: true,
												message: 'Please input your username!'
											}
										]
									})(
										<Input
											prefix={
												<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />
											}
											placeholder="Username"
										/>
									)}
								</Form.Item>
								<Form.Item>
									{getFieldDecorator('password', {
										rules: [
											{
												required: true,
												message: 'Please input your Password!'
											}
										]
									})(
										<Input
											prefix={
												<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />
											}
											type="password"
											placeholder="Password"
										/>
									)}
								</Form.Item>
								<Form.Item>
									{getFieldDecorator('remember', {
										valuePropName: 'checked',
										initialValue: true
									})(<Checkbox>Remember me</Checkbox>)}
									<Link to="/forgot" className="login-form-forgot">
										Forgot password
									</Link>
									<Button
										type="primary"
										htmlType="submit"
										className="login-form-button"
									>
										Log in
									</Button>
									<Divider>OR</Divider>
									Or <Link to="/register">register now!</Link>
								</Form.Item>
							</Form>
						</div>
					</Col>
				</Row>
				,
			</>
		)
	}
}

const USER_LOGIN = gql`
	mutation($userInput: LoginUserInput!) {
		login(userInput: $userInput) {
			token
		}
	}
`

export default withApollo(Form.create({ name: 'normal_login' })(Login))
